import React from 'react';
  
const SignUp = () => {
  return (
    <div>
    <h1>What is NC Covid Today?</h1>
    This is a web-app created for CS-4800 at Appalachian State University. The goal was to help share information about the Covid-19 pandemic in an easily understandable way. 
    North Carolina does have a [NC Covid Dashboard](https://covid19.ncdhhs.gov/dashboard), however this site seemed more focused towards lawmakers than the average person. 
    Information overload... Fill this in more as we finish up :) 
    </div>
      
      
  );
};



  
export default SignUp;